<script>
    import axios from "axios";
    import api from '../main';
    import crypto123 from 'crypto'
    const $http = axios.create({
        baseURL: api.url,
        timeout: 1000,
        headers: {'X-Custom-Header': 'foobar'}
    });
    import { Loading } from 'element-ui';

    export default {
        name: "AddStudentnumber",
        data() {
            return {
                addstudentForm: {
                    username: '',
                    sno: '',
                    password: ''
                },
                addstudentreportcardForm: {
                    sno: '',
                    examstatus: '',
                    examgrade: '',
                    spendtime: ''
                },
                rules: {
                    username: [
                        {required: true, message: '', trigger: 'blur' }
                    ],
                    sno: [
                        {required: true, message: '', trigger: 'blur' }
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur' }
                    ]
                },
                imgCode: "",
                code: "",
                requestForm: "",
                tableData: [],
                search: '',
                msg: '',
                ideatitystatus: '',

                errorInfoList:[], //
            };
        },
        created() {
            this.ideatitystatus = sessionStorage.getItem('isteacher')
            // const _this = this;
            // axios.get('http://localhost:8181/findAll').then(function (resp) {
            //     console.log(resp);
            // })
            if(this.ideatitystatus == 'teacher')
            {
                this.getCodeImage();
            }else
            {
                this.$message.error('');
            }
        },
        computed: {
            // 
            tables() {
                const search = this.search;
                if (search) {
                    // filter() 
                    //  filter() 
                    //  filter() 
                    return this.tableData.filter(data => {
                        // some() ;
                        // some() 
                        // true , ;
                        // false
                        //  some() 
                        //  some() 
                        return Object.keys(data).some(key => {
                            // indexOf() -1
                            // toLowerCase()
                            return String(data[key]).toLowerCase().indexOf(search) > -1
                        })
                    })
                }
                return this.tableData
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let phoneTest = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,50}$/;  //8-18

                        if (!phoneTest.test(this.addstudentForm.password)) {
                            this.$message.error('8-50');
                            return false;
                        }else
                        {
                            // MD5
                            const md5 = crypto123.createHash('md5');
                            md5.update(this.addstudentForm.password);
                            let aftermd5password = md5.digest('hex');
                            // this.$message.success('' + aftermd5password);
                            this.addstudentForm.password = aftermd5password;
                            const _this = this;
                            axios.post(api.url + '/addstudentnumber', this.addstudentForm).then(function (resp) {
                                if(resp.data.state)
                                {
                                    _this.$message.success(resp.data.msg);
                                }else
                                {
                                    _this.$message.error(resp.data.msg);
                                }
                            });
                            this.addstudentreportcardForm.sno = this.addstudentForm.sno;
                            axios.post(api.url + '/studentreportcard/addstudentrepotcard', this.addstudentreportcardForm).then(function (resp1) {
                                if(resp1.data)
                                {
                                    _this.$message.success(resp1.data);
                                }else
                                {
                                    _this.$message.error(resp1.data);
                                }
                            });
                        }

                    } else {
                        this.$message.error("Please input correct data!")
                        return false;
                    }
                });
            },
            submitList(object) {
                            // MD5
                            const md5 = crypto123.createHash('md5');
                            md5.update(object.password.toString());
                            let aftermd5password = md5.digest('hex');
                            // this.$message.success('' + aftermd5password);
                object.password = aftermd5password;
                            const _this = this;
                            axios.post(api.url + '/addstudentnumber', object).then(function (resp) {
                                if(resp.data.state)
                                {
                                    // _this.$message.success(resp.data.msg);
                                }else
                                {
                                    // _this.$message.error(resp.data.msg);
                                    _this.errorInfoList.push(object)
                                }
                            });
                            this.addstudentreportcardForm.sno = object.sno;
                            axios.post(api.url + '/studentreportcard/addstudentrepotcard', this.addstudentreportcardForm).then(function (resp1) {
                                if(resp1.data == "")
                                {
                                    // _this.$message.success(resp1.data);
                                }else
                                {
                                    // _this.$message.error(resp1.data);
                                }
                            });

            },
            //
            async getCodeImage() {
                const {data: res} = await $http.get("/getImage");
                // console.log("" + res.data);
                // console.log("base64", res);
                this.imgCode = "data:image/png;base64," + res;
            },
            IdentityCodeImage() {
                const _this = this;
                // console.log(this.code);
                axios.get(api.url + '/findAll/' + this.code).then(function (resp) {
                    // console.log(resp.data);
                    if(resp.data.state)
                    {
                        _this.tableData =resp.data.list;
                        _this.getCodeImage();
                        // console.log(_this.tableData);
                    }else
                    {
                        _this.$message.error("");
                    }

                })
            },
            handleEdit(row) {
                // console.log(index, row);
                // this.$router.push('/messageupdate') //
                //
                this.$router.push({
                    path: '/userupdate',
                    //
                    query: {
                        id: row.id
                    }
                })
            },
            handleDelete(row) {
                const _this = this;
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    axios.delete(api.url + '/deleteByUserId/' + row.id).then(function (resp) {
                        // console.log("resp.data:" + resp.data);
                        // _this.msg = resp.data;
                        if(resp.data == 1)
                        {
                            _this.$message.success("");
                        }else
                        {
                            _this.$message.error("");
                        }
                    });
                    // window.location.reload();//
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //
            tableRowClassName({row, rowIndex}) {
                let index = rowIndex + 1;
                if(index %2 == 0){
                    return 'warning-row'
                }
            },
    handleChange(file, fileList) {
        this.fileTemp = file.raw
        if (this.fileTemp) {
            if ((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') || (this.fileTemp.type == 'application/vnd.ms-excel')) {
                //
                this.importfxx(this.fileTemp);

            } else {
                this.$message({
                    type: 'warning',
                    message: ''
                })
            }
        } else {
            this.$message({
                type: 'warning',
                message: ''
            })
        }
    },
    importfxx(obj) {
        let _this = this;
        // DOM
        this.file = obj
        var rABS = false; //
        var f = this.file;
        var reader = new FileReader();
        //if (!FileReader.prototype.readAsBinaryString) {
        FileReader.prototype.readAsBinaryString = function (f) {
            var binary = "";
            var rABS = false; //
            var pt = this;
            var wb; //
            var outdata;
            var reader = new FileReader();
            reader.onload = function (e) {
                var bytes = new Uint8Array(reader.result);
                var length = bytes.byteLength;
                for (var i = 0; i < length; i++) {
                    binary += String.fromCharCode(bytes[i]);
                }
                var XLSX = require('xlsx');
                if (rABS) {
                    wb = XLSX.read(btoa(fixdata(binary)), { //
                        type: 'base64'
                    });
                } else {
                    wb = XLSX.read(binary, {
                        type: 'binary'
                    });
                }
                outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);//outdata
                this.da = [...outdata]
                // let arr = []


                //
                const loading = Loading.service({
                    lock: true,
                    text: '...',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });

                this.da.map(v => {
                    let obj = {}
                    obj.username = v['']
                    obj.password = v['']
                    obj.sno = v['']
                    // arr.push(obj)
                    console.log('obj');
                    console.log(obj);
                    _this.submitList(obj)
            })
                setTimeout(() => {
                    loading.close();
                    if (_this.errorInfoList.length == 0){
                        _this.$message.success("");
                    } else{
                        _this.$message.warning("");
                        _this.onClickDownDaily();
                    }
                }, 1500);
                _this.errorInfoList=[];

                /*this.$nextTick(() => { //  Loading 
                    loading.close();
                });
*/
            }
            reader.readAsArrayBuffer(f);
        }

        if (rABS) {
            reader.readAsArrayBuffer(f);
        } else {
            reader.readAsBinaryString(f);
        }
    },
            onClickDownDaily() {
                let title = ''
                let str=''
                this.errorInfoList.forEach(item=>{
                    str+=':'+item.username+'   '+':'+item.sno+'\r\n'
                })
                let allStr = title+'\r\n'+'\r\n'+str
                let export_blob = new Blob([allStr]);
                let save_link = document.createElement("a");
                save_link.href = window.URL.createObjectURL(export_blob);
                save_link.download = ''+'.txt';
                save_link.click();
                // let element = document.getElementById("a");
                // element.parentElement.removeChild(element)
            },
    }
}
</script>


